:root {
  font-family: "Source Sans Pro", sans-serif;
  letter-spacing: 1px;
  margin: 0;
  padding: 0;
  #dark {
    --lighter: #a2ced1;
    --accent: #68c8ce;
    --background-primary: #0a0d14;
    --background-secondary: #1c2438;
    --background-secondary-alt: #141a29;
    --foreground-primary: #a8b5c3;
    --purple: #492dd5ec;
  }
  #light {
    --lighter: #0a0d14;
    --accent: #1f3eb8;
    --background-primary: #f3f7ff;
    --background-secondary: #cbd6f0;
    --background-secondary-alt: #cbd6f0;
    --foreground-primary: #141a29;
    --purple: #492dd5ec;
  }
}

body {
  background-color: var(--accent);
  color: var(--accent);
  transition: background-color 1s ease-in-out;
  width: 100vw;
  margin: 0;
}

.container {
  min-height: 100vh;
  overflow-y: scroll;
  margin: 0 auto;
  padding: 0;
  background-color: var(--background-primary);
  transition: background-color 1s ease-in-out;
}
